import { useMemo } from 'react';
import { useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import { useLocalStorage } from 'react-use';
import { pick } from 'lodash';

import { getDefaultTheme, isMatchedById, randomSlug } from '@mssgme/shared';
import { isMatchId } from '@mssgme/helpers';
import { useFetching } from './useFetching';
import { useActions } from './useActions';
import { useCollections } from './useCollections';
import { useWorkspace } from './useWorkspace';

export function useWebsites() {
    return useSelector((state) => state.websites);
}

export function useWebsite(predicate) {
    const website = useSelector((state) => state.websites.find(predicate));
    const specifier = useMemo(() => ({ website: website?._id }), [website?._id]);
    const { crm } = useWorkspace();
    const { items: collections } = useCollections(specifier);

    return useMemo(() => {
        if (!website) {
            return {
                homePageId: null,
                pages: [],
                collections,
                crm,
            };
        }

        return {
            ...website,
            crm: website.crm.onBoarding ? { ...crm, onBoarding: true } : website.crm,
            collections,
        };
    }, [crm, website, collections]);
}

export function useWebsiteById(id) {
    return useWebsite(isMatchedById(id));
}

export function useWebsiteBySlug(slug) {
    return useWebsite(isMatchedById(slug)); //TODO: all routes replace website slug with id
}

export function clearWebsitePayload(website) {
    return pick(website, [
        'name',
        'slug',
        'seo',
        'tracking',
        'enabled',
        'branding',
        'embeddedCode',
        'scaffold',
        'menu',
        'theme',
        'localization',
    ]);
}

export function clearPagePayload(website) {
    return pick(website, ['enabled', 'name', 'slug', 'seo', 'tracking', 'theme']);
}

export function useWebsitePage(website, predicate) {
    const homePageId = website.homePageId;
    const page = website.pages.find(predicate || isMatchId(homePageId));

    return {
        website,
        page,
        homePageId,
        pageId: page?._id,
        websiteId: website._id,
    };
}

export function useWebsitePageBySlugs() {
    const { websiteSlug, pageSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);
    const predicate = pageSlug && isMatchId(pageSlug);

    return useWebsitePage(website, predicate);
}

export const useWebsitesFactory = () => {
    const history = useHistory();
    const { api } = useActions();
    const [helpTipVisible, setHelpTipVisible] = useLocalStorage('visualBuilderHelpTip', true);

    const { pending, run, ...state } = useFetching(async ({ templateId, theme, redirect = true } = {}) => {
        const result = await api.websites.scaffold({
            payload: {
                theme: theme || getDefaultTheme(),
                templateId,
            },
        });

        if (redirect) {
            if (helpTipVisible) {
                setHelpTipVisible(false);
            }

            history.push({
                pathname: `/pages/-/${result._id}/edit${helpTipVisible ? '/help' : ''}`,
            });
        }
    }, []);

    return {
        pending,
        create: run, // `run` here would not change, as its dependencies are `[]`
        link: '/pages/create',
        ...state,
    };
};

export const usePagesFactory = (websiteId) => {
    const history = useHistory();
    const { api } = useActions();
    const { run, ...state } = useFetching(async ({ templateId, theme, redirect = true } = {}) => {
        const name = 'Page';
        const result = await api.pages.create({
            websiteId,
            payload: {
                enabled: true,
                slug: randomSlug(5),
                theme: theme || getDefaultTheme(),
                name,
                templateId,
            },
        });

        if (redirect) {
            history.push({
                pathname: `/pages/-/${websiteId}/${result._id}/setup`,
            });
        }
    });

    return {
        create: run,
        ...state,
    };
};
